            <div class="topbar-divider d-none d-sm-block"></div>

            <!-- Nav Item - User Information -->
            <li class="nav-item dropdown no-arrow">
              <a class="nav-link dropdown-toggle" href="#" id="userDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                <span class="mr-2 d-none d-lg-inline text-gray-600 small">JBID: jsmit0201</span>
                <!-- <img class="img-profile rounded-circle" src="https://source.unsplash.com/QAB-WJcbgJk/60x60"> -->
              </a>
              <!-- Dropdown - User Information -->
              <div class="dropdown-menu dropdown-menu-right shadow animated--grow-in" aria-labelledby="userDropdown">
                <a class="dropdown-item" href="#">
                  <i class="fas fa-user fa-sm fa-fw mr-2 text-gray-400"></i>
                  Profile
                </a>
                <div class="dropdown-divider"></div>
                <router-link to="Login">
                <a class="dropdown-item" href="#" data-toggle="modal" data-target="#logoutModal">
                  <i class="fas fa-sign-out-alt fa-sm fa-fw mr-2 text-gray-400"></i>
                  Logout
                </a>
                </router-link>
              </div>
            </li>

          </ul>

        </nav>
        <!-- End of Topbar -->

        <!-- Begin Page Content -->
        <div class="container-fluid">

          <!-- Page Heading -->
          <div class="d-sm-flex align-items-center justify-content-between mb-4">
            <h1 class="h3 mb-0 text-gray-800">
              Message File Transfer (MFT)&nbsp;&nbsp;
              <span class="badge badge-services" style="padding: 5px 15px 5px 15px; font-size: 12px;">
                Services health&nbsp;&nbsp;&nbsp;<span style="color: green; font-weight: 450;">Up</span>&nbsp;: {{countUpService}}&nbsp;&nbsp;&nbsp;
                <span style="color: red; font-weight: 450;">Down</span>&nbsp;: {{countDownService}}
              </span>
            </h1>
          </div>

          <!-- Content Row -->
          <div class="row">

            <!-- Earnings (Monthly) Card Example -->
            <div class="col-xl-3 col-md-6 mb-4">
              <div class="card border-left-success shadow h-100 py-2">
                <div class="card-body">
                  <div class="row no-gutters align-items-center">
                    <div class="col mr-2">
                      <div class="text-xs font-weight-bold text-success text-uppercase mb-1">Completed</div>
                      <div class="h5 mb-0 font-weight-bold text-gray-800">No service</div>
                    </div>
                    <div class="col-auto">
                      <i class="fas fa-check fa-2x text-gray-300"></i>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <!-- Earnings (Monthly) Card Example -->
            <div class="col-xl-3 col-md-6 mb-4">
              <div class="card border-left-primary shadow h-100 py-2">
                <div class="card-body">
                  <div class="row no-gutters align-items-center">
                    <div class="col mr-2">
                      <div class="text-xs font-weight-bold text-primary text-uppercase mb-1">Processing</div>
                      <div class="h5 mb-0 font-weight-bold text-gray-800">No service</div>
                    </div>
                    <div class="col-auto">
                      <i class="fas fa-spinner fa-2x text-gray-300"></i>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <!-- Earnings (Monthly) Card Example -->
            <div class="col-xl-3 col-md-6 mb-4">
              <div class="card border-left-warning shadow h-100 py-2">
                <div class="card-body">
                  <div class="row no-gutters align-items-center">
                    <div class="col mr-2">
                      <div class="text-xs font-weight-bold text-warning text-uppercase mb-1">Reprocessing</div>
                      <div class="h5 mb-0 font-weight-bold text-gray-800">No service</div>
                    </div>
                    <div class="col-auto">
                      <i class="fas fa-sync fa-2x text-gray-300"></i>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <!-- Pending Requests Card Example -->
            <div class="col-xl-3 col-md-6 mb-4">
              <div class="card border-left-danger shadow h-100 py-2">
                <div class="card-body">
                  <div class="row no-gutters align-items-center">
                    <div class="col mr-2">
                      <div class="text-xs font-weight-bold text-danger text-uppercase mb-1">Errors</div>
                      <div class="h5 mb-0 font-weight-bold text-gray-800">No service</div>
                    </div>
                    <div class="col-auto">
                      <i class="fas fa-times-circle fa-2x text-gray-300"></i>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Content Row -->
          <div class="row">

            <!-- Content Column -->
            <div class="col-lg-12 col-md-12 col-12 mb-4">

              <!-- Project Card Example -->
              <div class="card shadow mb-4">
                <div class="card-header py-3">
                  <!-- <h6 class="m-0 font-weight-bold text-primary">{{countService}} Services <span class="float-right"><span style="color: green; font-weight: 450;">Up</span>&nbsp;<img src="../img/sunny.png" style="margin-top: -3px; width: 20px; height: 20px;">&nbsp;: {{countUpService}}&nbsp;&nbsp;&nbsp;&nbsp;<span style="color: red; font-weight: 450;">Down</span>&nbsp;<img src="../img/cloudy.png" style="width: 20px; height: 20px;">&nbsp;: {{countDownService}}</span></h6> -->
                  <h6 class="m-0 font-weight-bold text-primary">MFT Data </h6>
                </div>
                <div class="card-body">
                  <div class="table-responsive">
                      <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
                        <thead>
                          <tr>
                            <th>CMB ID</th>
                            <th>Correlation ID</th>
                            <th>File</th>
                            <th>Provider</th>
                            <th>Consumer</th>
                            <th>Origin</th>
                            <th>Status</th>
                            <th>Start <i class="far fa-clock"></i></th>
                            <th>End <i class="far fa-clock"></i></th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td>no data</td>
                            <td>no data</td>
                            <td>no data</td>
                            <td>no data</td>
                            <td>no data</td>
                            <td>no data</td>
                            <td>no data</td>
                            <td>no data</td>
                            <td>no data</td>
                          </tr>
                        </tbody>
                      </table>
                  </div>
                </div>
              </div>
            </div>
          </div>

        </div>
        <!-- /.container-fluid -->

      </div>
      <!-- End of Main Content -->

      <!-- Footer -->
      <footer class="sticky-footer bg-white">
        <div class="container my-auto">
          <div class="copyright text-center my-auto">
            <span>Copyright &copy; Cloud Native | Cumulus | Census Bureau 2020</span>
          </div>
        </div>
      </footer>
      <!-- End of Footer -->

    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Home',
  mounted() {
    if (localStorage.getItem('reloaded')) {
        // The page was just reloaded. Clear the value from local storage
        // so that it will reload the next time this page is visited.
        localStorage.removeItem('reloaded');
    } else {
        // Set a flag so that we know not to reload the page twice.
        localStorage.setItem('reloaded', '1');
        location.reload();
    }
  },
  data () {
    return {
      deliveredFirstTry: '',
      deliveredFirstTryCount: '',
      failedFirstTry: '',
      failedFirstTryCount: '',
      upService: '',
      countService: '',
      countUpService: 0,
      countDownService: 0
    }
  },
  created () {
      // Delivered on First Try service ------------------------------------------------------------------------------------------
      axios.get('http://cumulus-monitor-api.dev.api.paradymelab.com/system/status/instant?query=routing_service_message_delivery_successful_total')
      .then(response => {
        this.deliveredFirstTry = response.data
        console.log(this.deliveredFirstTry)
        this.deliveredFirstTryCount = this.deliveredFirstTry.data.result[0].value[1]
        // if (this.deliveredFirstTry == null) {
        //   this.deliveredFirstTryCount = "Service Down"
        // } else {
        //   this.deliveredFirstTryCount = this.deliveredFirstTry.data.result[0].value[1]
        // }
      })
      // Error count service ------------------------------------------------------------------------------------------
      axios.get('http://cumulus-monitor-api.dev.api.paradymelab.com/system/status/instant?query=routing_service_message_delivery_fail_total')
      .then(response => {
        this.failedFirstTry = response.data
        console.log(this.failedFirstTry)
        this.failedFirstTryCount = this.failedFirstTry.data.result[0].value[1]
      })
      // UP service ------------------------------------------------------------------------------------------
      axios.get('http://cumulus-monitor-api.dev.api.paradymelab.com/system/status/instant?query=up')
      .then(response => {
        this.upService = response.data
        console.log(this.upService)
        this.countService = this.upService.data.result.length
        var i
        for (i = 0; i < this.countService; i++) {
          if (this.upService.data.result[i].value[1] == "1") {
            this.countUpService++
          } else {
            this.countDownService++
          }
        }
      })
  }
}
</script>



