<script>
import axios from 'axios'

// Vue.component('modal', {
//     data: function () {
//         console.log("### DATA");
//     },
// });

export default {
  name: 'Hospital',
  mounted () {
    if (localStorage.getItem('reloaded')) {
      // The page was just reloaded. Clear the value from local storage
      // so that it will reload the next time this page is visited.
      localStorage.removeItem('reloaded')
    } else {
      // Set a flag so that we know not to reload the page twice.
      localStorage.setItem('reloaded', '1')
      location.reload()
    }
  },
  data () {
    return {
      startDate: '2019-01-15T01:00:00.000Z',
      endDate: '2020-01-15T01:00:00.000Z',
      hospitalCount: 0,
      upService: '',
      countService: '',
      countUpService: 0,
      countDownService: 0,
      code: ''
    }
  },
  created () {
    // UP service ------------------------------------------------------------------------------------------
    axios.get('http://cumulus-monitor-api.dev.api.paradymelab.com/system/status/instant?query=up')
    .then(response => {
      this.upService = response.data
      console.log(this.upService)
      this.countService = this.upService.data.result.length
      var i
      for (i = 0; i < this.countService; i++) {
        if (this.upService.data.result[i].value[1] == "1") {
          this.countUpService++
        } else {
          this.countDownService++
        }
      }
    })
    let url = location.href
    if (url === 'http://localhost:8080/#/Hospital' || 'http://cumulus-dashboard.dev.api.paradymelab.com/#/Hospital'){
      axios.get('http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start=2018-03-05T01:00:00.000Z&end=2020-01-15T01:00:00.000Z')
      .then(response => {
        console.log(response)
        let data = []
        var i
        for (i = 0; i < 5; i++){
          data.push(response.data[i])
        }
        this.table = data
        console.log(this.table)
        // this.hospitalCount = response.data.length
        this.hospitalCount = 5
       // http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start='+this.startDate+'&end='+this.endDate)
      })
    } else {
      axios.get('http://cumulus-error-processor-api-dev-cumulus.labcontainers.census.gov/error/details/query_range?query=firstTriedTS&start=2018-03-05T01:00:00.000Z&end=2020-01-15T01:00:00.000Z')
      .then(response => {
        console.log(response)
        this.table = response.data
        this.hospitalCount = response.data.length
       // http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start='+this.startDate+'&end='+this.endDate)
      })
    }
  },
  methods: {
    reloadPage () {
      location.reload()
    },
    sendCode (code) {
      this.code = code
    }
  }
}
</script>

