import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Hospital from '@/components/Hospital'
import Login from '@/components/Login'
import API from '@/components/API'
import KAFKA from '@/components/Kafka'
import MFT from '@/components/MFT'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/hospital',
      name: 'Hospital',
      component: Hospital
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/api',
      name: 'API',
      component: API
    },
    {
      path: '/kafka',
      name: 'KAFKA',
      component: KAFKA
    },
    {
      path: '/mft',
      name: 'MFT',
      component: MFT
    },
  ]
})



