            <div class='col-12'>

                <div class='card' style='width:70%; margin-left: 15%; height: 60vh; margin-top: 5vh; border:none; background-color: #112e51 !important; box-shadow: 5px 5px 7px #09182b;'>
                    <!-- <img class='card-img-top' src='img_avatar1.png' alt='Card image'> -->
                    <div class='card-header' style='color: white; font-size: 20px; padding: 25px 15px 25px 25px; background-color: #112e51 !important;'>
                        <img src='../../static/img/lock.png' alt='Italian Trulli' style='width: 35px; height: 35px; margin-top: -7px;'>&nbsp;&nbsp;&nbsp;Event Delivery Monitor Dashboard
                    </div>
                    <div class='card-body'>
                        <div class='row'>
                            <div class='col-6 text-left' style='padding: 30px;'>
                                <!-- <h2 style='color: white;'>Sign In</h2> -->
                                <label style='color:white;'>Username</label><br>
                                <input style='width: 100%; border: none; border-radius: 5px; height: 35px; padding-left: 15px;' type='text' placeholder='Enter Username' name='uname' required><br>
                                <br>
                                <label style='color:white;'>Password</label><br>
                                <input style='width: 100%; border: none; border-radius: 5px; height: 35px; padding-left: 15px;' type='password' placeholder='Enter Password' name='psw' required><br>
                                <br><br>
                                <router-link to='/'><button style='background-color: #0071bc; border: none; width: 100%;' type='button' class='btn btn-primary float-right'>
                                    <i style='color:white;' class='fa fa-lock'></i>&nbsp;&nbsp;Log On
                                </button></router-link>
                                <!-- <br><br><br><br><br>
                                <div class='row'>
                                    <div class='col-12 text-center' style='color: white; font-size: 12px;'>
                                        <a style='color: white;' href=''><u>Forgot Password</u></a>
                                        &nbsp;|&nbsp;
                                        <a style='color: white;' href=''><u>Change Password</u></a>
                                    </div>
                                </div> -->
                            </div>
                            <div class='col-6'>
                                <p style='font-size: 12px; color: white; margin: 30px 0 0 0; font-weight:400;'>
                                    US Census Bureau Notice and Consent Warning. This is a Census Bureau computer system. Census Bureau computer systems are provided for the processing of official U.S. Government information only. All data contained within Census Bureau computer systems are owned by the Census Bureau and may be monitored, intercepted, recorded, read, copied, or captured in any manner, and disclosed in any manner by authorized personnel. THERE IS NO RIGHT OF PRIVACY IN THIS SYSTEM. System personnel may disclose any potential evidence of crime found on Census Bureau computer systems to appropriate authorities. USE OF THIS SYSTEM BY ANY USER, AUTHORIZED OR UNAUTHORIZED, CONSTITUTES CONSENT TO MONITORING, INTERCEPTION, RECORDING, READING, COPYING, CAPTURING, AND DISCLOSURE OF COMPUTER ACTIVITY. Use of this computer without authorization or for unauthorized purposes is a violation of federal law and punishable by fines or imprisonment (Public Law 99-474).
                                </p>
                            </div>
                        <br>
                        </div>
                    </div>
                    <div class='card-footer' style="background-color: #112e51 !important;">
                        <div class='row'>
                            <div class='col-12 text-center' style='color: white; font-size: 12px;'>
                                <a style='color: white;' href=''><u>Forgot Password</u></a>
                                &nbsp;|&nbsp;
                                <a style='color: white;' href=''><u>Change Password</u></a>
                                &nbsp;|&nbsp;
                                <a style='color: white;' href=''><u>Need Access?</u></a>
                            </div>
                            <!-- <div class='col-6 text-center' style='color: white;'>
                                <a style='color: white; font-size: 12px;' href=''><u>Change Password</u></a>
                            </div> -->
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
    <!-- 3. Footer -->
    <!-- <footer class='section footer-classic context-dark bg-image' style='background-color: #112e51; height: 50px;'>
        ss
    </footer> -->

    <!-- <script src='https://code.jquery.com/jquery-3.2.1.slim.min.js' integrity='sha384-KJ3o2DKtIkvYIK3UENzmM7KCkRr/rE9/Qpg6aAZGJwFDMVNA/GpGFF93hXpG5KkN' crossorigin='anonymous'></script>
    <script src='https://code.jquery.com/jquery-3.3.1.min.js' integrity='sha256-FgpCb/KJQlLNfOu91ta32o/NMZxltwRo8QtmkMRdAu8=' crossorigin='anonymous'></script>
    <script src='https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.12.9/umd/popper.min.js' integrity='sha384-ApNbgh9B+Y1QKtv3Rn7W3mgPxhU9K/ScQsAP7hUibX39j7fakFPskvXusvfa0b4Q' crossorigin='anonymous'></script>
    <script src='https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/js/bootstrap.min.js' integrity='sha384-JZR6Spejh4U02d8jOt6vLEHfe/JQGiRRSQQxSfFWpi1MquVdAyjUar5+76PVCmYl' crossorigin='anonymous'></script> -->
  </div>
</template>

<script>
import axios from 'axios'
// import LineChart from '@/components/LineChart'
export default {
  name: 'login',
  mounted () {
    if (localStorage.getItem('reloaded')) {
      // The page was just reloaded. Clear the value from local storage
      // so that it will reload the next time this page is visited.
      localStorage.removeItem('reloaded')
    } else {
      // Set a flag so that we know not to reload the page twice.
      localStorage.setItem('reloaded', '1')
      location.reload()
    }
  },
  data () {
    return {
      posts: '',
      dummyPosts: '',
      choiceText: new Date(Date.now() - 60000),
      selected: '',
      timeInterval: [
        {value: new Date(Date.now() - 60000), pastTime: 'Past 1 Minute'},
        {value: new Date(Date.now() - 300000), pastTime: 'Past 5 Minutes'},
        {value: new Date(Date.now() - 600000), pastTime: 'Past 10 Minutes'}
      ],
      choiceId: '',
      // countService: upService.data.result.length,
      // upService: upService,
      // successFailedService: successFailedService,
      // latencyService : latencyService,
      // ---------------------------------------------------- API
      successLatency095: '',
      successLatency099: '',
      failureLatency095: '',
      failureLatency099: '',
      countService: '',
      upService: '',
      successFailedService: '',
      successCount: '',
      failureCount: '',

      // Array will be automatically processed with visualization.arrayToDataTable function
      areaChartData: [
        ['Date', 'First Try', 'Retry', 'Failed'],
        ['Jan 12', 2000, 820, 1800],
        ['Jan 13', 1900, 820, 800],
        ['Jan 14', 1930, 540, 750],
        ['Jan 15', 2830, 840, 550],
        ['Jan 16', 1930, 840, 1950],
        ['Jan 17', 1830, 840, 1550],
        ['Jan 18', 2530, 840, 500]
      ],
      areaChartOptions: {
        title: 'Performance Comparison',
        hAxis: {title: 'Week', titleTextStyle: {color: '#333'}},
        vAxis: {minValue: 0},
        colors: ['#4cbb17', '#0073cf', '#ff0800']
      },
      lineChartData: [
        ['Date', 'Request Timeout', '504 Gateway Timeout', 'Message Rejected'],
        ['Jan 1', 20, 100, 50],
        ['Jan 2', 170, 70, 20],
        ['Jan 3', 17, 100, 140],
        ['Jan 4', 70, 40, 120]
      ],
      lineChartOptions: {
        colors: ['#fd6a02', '#ffd300', '#39ff14']
      },
      // Reference Use Only
      time_default: new Date(),
      time_1min_ago: new Date(Date.now() - 60000),
      time_5min_ago: new Date(Date.now() - 300000),
      time_10min_ago: new Date(Date.now() - 600000)
      //
    }
  },
  created () {
    // this.$forceUpdate()
    this.selected = this.timeInterval[0].pastTime
    this.login = true
    const requestBody = {
      grant_type: 'password',
      client_id: 'collection-event-inbound-processor',
      username: 'dayong',
      password: 'dayong',
      client_secret: '0f265040-716a-4f3b-a403-cabd60685b42'
    }
    const config = {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }
    const url = 'http://keycloak.dev.istio.paradymelab.com/auth/realms/cumulus/protocol/openid-connect/token'
    axios.post(url, requestBody, config)
    .then((result) => {
      console.log(result)
    })
    .catch((err) => {
      console.log(err)
    })
    // ----------------------------------------------------- UP service
    // axios.get(`/static/up_api_service.json`)
    axios.get('http://prometheus-route-monitoring.labcontainers.census.gov/api/v1/query?query=up&start=' + this.choiceText + '&end=' + this.time_default)
      .then(response => {
        // JSON responses are automatically parsed.
        this.upService = response.data
        this.countService = this.upService.data.result.length
        console.log(this.upService)
        console.log(this.countService)
      })
    // ----------------------------------------------------- Delivered & Failed service
    // axios.get(`/static/success_failed_service.json`)
    axios.get('http://prometheus-route-monitoring.labcontainers.census.gov/api/v1/query?query=routing_service_message_delivery_timer_seconds_count&start=' + this.choiceText + '&end=' + this.time_default)
      .then(response => {
        // JSON responses are automatically parsed.
        this.successFailedService = response.data
        var i
        for (i = 0; i < response.data.data.result.length; i++) {
          if (response.data.data.result[i].metric.PROCESS_STATUS === 'SUCCESS') {
            this.successCount = response.data.data.result[i].value[1]
          } else {
            this.failureCount = response.data.data.result[i].value[1]
          }
        }
        console.log(this.successFailedService)
      })
      // ----------------------------------------------------- Latency
      // axios.get('/static/latency_service.json')
    axios.get('http://prometheus-route-monitoring.labcontainers.census.gov/api/v1/query?query=routing_service_message_delivery_timer_seconds&start=' + this.choiceText + '&end=' + this.time_default)
      .then(response => {
        // JSON responses are automatically parsed.
        console.log(response.data.data.result)
        this.latencyService = response.data
        var i
        for (i = 0; i < response.data.data.result.length; i++) {
          if (response.data.data.result[i].metric.PROCESS_STATUS === 'SUCCESS' && response.data.data.result[i].metric.quantile === '0.95') {
            this.successLatency095 = (Number(response.data.data.result[i].value[1]) * 1000).toFixed(2)
          } else if (response.data.data.result[i].metric.PROCESS_STATUS === 'SUCCESS' && response.data.data.result[i].metric.quantile === '0.99') {
            this.successLatency099 = (Number(response.data.data.result[i].value[1]) * 1000).toFixed(2)
          } else if (response.data.data.result[i].metric.PROCESS_STATUS === 'FAILURE' && response.data.data.result[i].metric.quantile === '0.95') {
            this.failureLatency095 = (Number(response.data.data.result[i].value[1]) * 1000).toFixed(2)
          } else if (response.data.data.result[i].metric.PROCESS_STATUS === 'FAILURE' && response.data.data.result[i].metric.quantile === '0.99') {
            this.failureLatency099 = (Number(response.data.data.result[i].value[1]) * 1000).toFixed(2)
          }
        }
        // console.log(this.latencyService)
      })
  },

  methods: {
    getTime () {
      // var values = this.timeInterval.map(function(o) { return o.value })
      var index
      if (this.timeInterval[0].pastTime === this.selected) {
        index = 0
      } else if (this.timeInterval[1].pastTime === this.selected) {
        index = 1
      } else if (this.timeInterval[2].pastTime === this.selected) {
        index = 2
      }
      console.log(index)
      this.choiceText = this.timeInterval[index].value
      this.selected = this.timeInterval[index].pastTime
      // this.location.reload()
    },
    reLoad () {
      this.location.reload()
    }
  }
}
</script>
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



