<template>
  <div id="app">
    <div id="wrapper">
    <navbar v-if="!login"></navbar>
    <router-view/>
    </div>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
export default {
  name: 'App',
  components: {
    'navbar': Navbar
  },
  data () {
      return {
          login: false
      }
  },
  created () {
      this.login = false
    //   if (window.location.href === 'http://localhost:8080/#/Login'){
      if (window.location.href === 'http://cumulus-dashboard.dev.api.paradymelab.com/index.html#/Login'){
        this.login = true
      } else if(window.location.href === 'http://localhost:8080/index.html#/Login') {
        this.login = true
      } else {
        this.login = false
      }
      console.log(window.location.href)
  }
}
</script>

<style>
@import url("https://use.fontawesome.com/releases/v5.7.2/css/all.css");
@import url("../css/sb-admin-2.min.css");
@import url("../css/sb-admin-2.css");
#app {
  padding: 0px 0px 0px 0px;
}
</style>



