                </a>
                <!-- <a class="dropdown-item" href="#">
                  <i class="fas fa-cogs fa-sm fa-fw mr-2 text-gray-400"></i>
                  Settings
                </a>
                <a class="dropdown-item" href="#">
                  <i class="fas fa-list fa-sm fa-fw mr-2 text-gray-400"></i>
                  Activity Log
                </a> -->
                <div class="dropdown-divider"></div>
                <router-link to="Login">
                <a class="dropdown-item" href="#" data-toggle="modal" data-target="#logoutModal">
                  <i class="fas fa-sign-out-alt fa-sm fa-fw mr-2 text-gray-400"></i>
                  Logout
                </a>
                </router-link>
              </div>
            </li>

          </ul>

        </nav>
        <!-- End of Topbar -->

        <!-- Begin Page Content -->
        <div class="container-fluid">

          <!-- Page Heading -->
          <div class="d-sm-flex align-items-center justify-content-between mb-4">
            <h1 class="h3 mb-0 text-gray-800">Error Hospital</h1>
          </div>

          <!-- Content Row -->
          <div class="row">

            <!-- Earnings (Monthly) Card Example -->
            <div class="col-xl-6 col-lg-6 col-md-6 col-6 mb-4">
              <div class="card border-left-danger shadow h-100 py-2">
                <div class="card-body">
                  <div class="row align-items-center">
                    <div class="col-6">
                      <div class="row">
                        <div class="col-12">
                          <div class="text-xs font-weight-bold text-danger text-uppercase mb-1">Error Count</div>
                          <div class="h5 mb-0 font-weight-bold text-gray-800">{{hospitalCount}}</div>
                          <p style="font-size:12px;">Last Updated: {{endDate}}</p>
                        </div>
                      </div>
                    </div>
                    <div class="col-6">
                      <div class="row">
                        <div class="col-lg-6 col-md-6 col-sm-6 col-6" style="margin:0; padding:0;">
                          <a href="#" style="width: 99%;" class="btn btn-success">
                            <span class="text small"><i class="fas fa-paper-plane"></i>&nbsp;&nbsp;Re-send All&nbsp;&nbsp;&nbsp;<span class="badge badge-light" style="padding: 5px 15px 5px 15px;"><b>{{hospitalCount}}</b></span></span>
                          </a>
                        </div>
                        <div class="col-lg-6 col-md-6 col-sm-6 col-6" style="margin:0; padding:0;">
                          <a href="#" @click="reloadPage()" style="height: 37px; width: 99%;" class="btn btn-light">
                              <i class="fas fa-sync-alt text-gray-600"></i>
                          </a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <!-- Earnings (Monthly) Card Example -->
            <div class="col-xl-6 col-lg-6 col-md-6 col-6 mb-4">
              <div class="card border-left-primary shadow h-100 py-2">
                <div class="card-body" style="padding: 25px 30px 0 30px;">
                  <div class="row no-gutters align-items-center">
                      <div class="row" style="margin-top: 15px;">
                          <!-- <label for="example-datetime-local-input" class="col-2 col-form-label">Date and time</label> -->
                          <div class="col-5" style="padding:0; margin:0;">
                            <!-- <label>Start Date :</label> -->
                            <div class="row">
                              <p class="col-3 text-right">From :</p>
                              <input @change="newDates()" v-model="startDate" class="col-9 form-control" style="width: 100%" type="datetime-local" id="example-datetime-local-input">
                            </div>
                          </div>
                          <div class="col-5" style="padding:0; margin: 0;">
                            <!-- <label>End Date :</label> -->
                            <div class="row">
                                <p class="col-3 text-right">To :</p>
                                <input @change="newDates()" v-model="endDate" class="col-9 form-control" style="width: 100%" type="datetime-local"  id="example-datetime-local-input">
                            </div>
                          </div>
                          <div class="col-2" style="padding: 0 0 0 20px; margin: 0;">
                            <btn @click="search()" style="width: 100%" class="btn btn-primary btn-icon-split float-right">
                              <!-- <span class="icon text-white-50">
                                <i class="fas fa-paper-plane"></i>
                              </span> -->
                              <span class="text">Search</span>
                            </btn>
                          </div>
                      </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- Content Row -->
          <div class="row">

            <!-- Content Column -->
            <div class="col-lg-12 col-md-12 col-12 mb-4">

              <!-- Project Card Example -->
              <div class="card shadow mb-4">
                <div class="card-header py-3">
                  <h6 class="m-0 font-weight-bold text-primary">Hospital Table</h6>
                </div>
                <div class="card-body">
                    <div class="table-responsive">
                        <table class="table table-bordered" id="dataTable" width="100%" cellspacing="0">
                          <thead>
                            <tr>
                              <th>Date/Time</th>
                              <th>Conversation ID</th>
                              <th>Integration Name</th>
                              <th>Sender</th>
                              <th>Target</th>
                              <th>HTTP!</th>
                              <th>Error Reason</th>
                              <th>Resend</th>

                            </tr>
                          </thead>
                          <tbody>
                            <tr  v-for="(value, index) in table" :key="index">
                              <td class="col-1" v-format.date>{{value.firstTriedTS}}</td>
                              <td class="col-3">{{value.cbmId}}</td>
                              <td class="col-1">{{value.operation}}</td>
                              <td class="col-1">{{value.sender}}</td>
                              <td class="col-1">{{value.target}}</td>
                              <td>
                                <a @click="sendCode('408')" href="#" class="btn btn-primary" data-toggle="modal" data-target="#myModal" data-backdrop="static" data-keyboard="false">
                                  <span class="text">408</span>
                                </a>
                              </td>
                              <td class="col-3">{{value.errorMsg || 'Unknown error msg.'}} </td>
                              <!-- <td class="col-1"><button type="button" class="btn btn-light enable_btn" id="resend_btn"><span id="resend_count">{{value.retryCount}}</span>&nbsp;<i class="fa fa-step-forward"></i>&nbsp;&nbsp;<span class="countdown"></span></button></td> -->
                              <td>
                                <a href="#" class="btn btn-success btn-icon-split">
                                  <span class="icon text-white-50">
                                    <i class="fas fa-paper-plane"></i>
                                  </span>
                                  <span class="text">{{value.retryCount || 0}}</span>
                                </a>
                              </td>
                            </tr>
                            <!-- Sample Modal Begin _________________________________________________________________________________________ -->

                                <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
                                  <div class="modal-dialog" role="document">
                                    <div class="modal-content">
                                      <div class="modal-header">
                                        <h4 class="modal-title text-left" id="myModalLabel"><span class="badge badge-primary">{{code}}</span>&nbsp;<span class="badge badge-light">Request Timeout</span></h4>
                                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                                      </div>
                                      <div class="modal-body">
                                        <div class="row">
                                          <div class="col-12">
                                            <ul class="row nav nav-tabs" role="tablist">
                                              <li class="col-3 nav-item text-center">
                                                <a class="nav-link active" data-toggle="tab" href="#home" role="tab">Definition</a>
                                              </li>
                                              <li class="col-3 nav-item text-center">
                                                <a class="nav-link" data-toggle="tab" href="#profile" role="tab">Error Reason</a>
                                              </li>
                                              <li class="col-3 nav-item text-center">
                                                <a class="nav-link" data-toggle="tab" href="#messages" role="tab">Suggestions</a>
                                              </li>
                                              <li class="col-3" style="display: inline-block;">
                                                <p class="float-right" style="padding:0; margin: 10px 10px 0 30px;">
                                                  Retries: 2
                                                </p>
                                              </li>
                                            </ul>

                                            <!-- Tab panes -->
                                            <div style="background-color: white; border: 1px solid #f1f1f1;" class="tab-content">
                                              <div class="tab-pane active" id="home" role="tabpanel">
                                                <div class="col-12" style="height: 20vh">
                                                  <br>
                                                  <p>
                                                    The server timed out waiting for the request. According to HTTP specifications: "The client did not produce a request within the time that the server was prepared to wait. The client MAY repeat the request without modifications at any later time.
                                                  </p>
                                                </div>
                                              </div>
                                              <div class="tab-pane" id="profile" role="tabpanel">
                                                <div class="col-12" style="height: 20vh">
                                                  <br>
                                                  <p>
                                                    java.util.concurrent.ExecutionException: org.apache.kafka.common.errors.TimeoutException: Failed to update metadata after 15000 ms.
                                                  </p>
                                                </div>
                                              </div>
                                              <div class="tab-pane" id="messages" role="tabpanel">
                                                <div class="col-12" style="height: 20vh">
                                                  <br>
                                                  <p>
                                                    1. Since there were only 2 retries, we suggeset to try "Re-send" once more.<br>
                                                    2. Reach out to the "Sender" endpoint.
                                                  </p>
                                                </div>
                                              </div>
                                            </div>
                                          </div>
                                          <hr>
                                          <div class="col-12">
                                            <ul class="row nav nav-tabs" role="tablist">
                                              <li class="col-4 nav-item text-center">
                                                <a class="nav-link active" data-toggle="tab" href="#requestSent" role="tab">Request Sent</a>
                                              </li>
                                              <li class="col-4 nav-item text-center">
                                                <a class="nav-link" data-toggle="tab" href="#errorResponse" role="tab">Error Response</a>
                                              </li>
                                              <li class="col-4 nav-item text-center">
                                                <a class="nav-link" data-toggle="tab" href="#origMsg" role="tab">Original Message</a>
                                              </li>
                                            </ul>

                                            <!-- Tab panes -->
                                            <div style="background-color: white; border: 1px solid #f1f1f1;" class="tab-content">
                                              <div class="tab-pane active" id="requestSent" role="tabpanel">
                                                <div class="col-12" style="height: 45vh; overflow: auto;">
                                                  <pre>
                                                    <code>
                                                      {{upService}}
                                                    </code>
                                                  </pre>
                                                </div>
                                              </div>
                                              <div class="tab-pane" id="errorResponse" role="tabpanel">
                                                <div class="col-12" style="height: 45vh; overflow: auto;">
                                                  <pre>
                                                    <code>
                                                      {{upService}}
                                                    </code>
                                                  </pre>
                                                </div>
                                              </div>
                                              <div class="tab-pane" id="origMsg" role="tabpanel">
                                                <div class="col-12" style="height: 45vh; overflow: auto;">
                                                  <pre>
                                                    <code>
                                                      {{upService}}
                                                    </code>
                                                  </pre>
                                                </div>
                                              </div>
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                      <!-- <div class="modal-footer">
                                        <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
                                        <button type="button" class="btn btn-primary">Save changes</button>
                                      </div> -->
                                    </div>
                                  </div>
                                </div>

                            <!-- Sample Modal End _________________________________________________________________________________________ -->
                          </tbody>
                        </table>
                      </div>
                </div>
              </div>

            </div>
          </div>

        </div>
        <!-- /.container-fluid -->

