      <!-- Footer -->
      <footer class="sticky-footer bg-white">
        <div class="container my-auto">
          <div class="copyright text-center my-auto">
            <span>Copyright &copy; Cloud Native | Cumulus | Census Bureau 2020</span>
          </div>
        </div>
      </footer>
      <!-- End of Footer -->

    </div>
    <!-- End of Content Wrapper -->
</template>

<script>
import axios from 'axios'

// Vue.component('modal', {
//     data: function () {
//         console.log("### DATA");
//     },
// });

export default {
  name: 'Hospital',
  mounted () {
    if (localStorage.getItem('reloaded')) {
      // The page was just reloaded. Clear the value from local storage
      // so that it will reload the next time this page is visited.
      localStorage.removeItem('reloaded')
    } else {
      // Set a flag so that we know not to reload the page twice.
      localStorage.setItem('reloaded', '1')
      location.reload()
    }
  },
  data () {
    return {
      startDate: '',
      endDate: '',
      serviceEndDate: '',
      serviceStartDate: '',
      hospitalCount: 0,
      upService: '',
      code: ''
    }
  },
  created () {
    let a = new Date()
    let r = new Date()
    let y = a.setDate(a.getDate() - 1)
    let z = new Date(y).toISOString()
    let x = z[16] + z[17] + z[18] + z[19] + z[20] + z[21] + z[22] + z[23]
    let b = r.toISOString()
    let c = b[16] + b[17] + b[18] + b[19] + b[20] + b[21] + b[22] + b[23]
    this.endDate = b.replace(c, "")
    this.startDate = z.replace(x, "")
    this.serviceEndDate = new Date(this.endDate).toISOString()
    this.serviceStartDate = new Date(this.startDate).toISOString()
    console.log(new Date(this.endDate))
    console.log(this.startDate)
    let url = location.href
    if (url === 'http://localhost:8080/#/Hospital' || 'http://cumulus-dashboard.dev.api.paradymelab.com/#/Hospital'){
      axios.get('http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start=2019-04-01T19:50:00.000Z&end=2019-04-18T19:50:00.000Z')
      .then(response => {
        console.log(response)
        let data = []
        var i
        for (i = 0; i < 5; i++){
          data.push(response.data[i])
        }
        this.table = data
        console.log(this.table)
        // this.hospitalCount = response.data.length
        this.hospitalCount = this.table.length
       // http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start='+this.startDate+'&end='+this.endDate)
      })
    } else {
      axios.get('http://cumulus-error-processor-api-dev-cumulus.labcontainers.census.gov/error/details/query_range?query=firstTriedTS&start='+this.serviceStartDate+'&end='+this.serviceEndDate)
      .then(response => {
        console.log(response)
        this.table = response.data
        this.hospitalCount = response.data.length
       // http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start='+this.startDate+'&end='+this.endDate)
      })
    }
  },
  methods: {
    reloadPage () {
      location.reload()
    },
    sendCode (code) {
      this.code = code
    },
    newDates () {
      this.serviceStartDate = new Date(this.startDate).toISOString()
      this.serviceEndDate = new Date(this.endDate).toISOString()
    },
    search () {
      let url = location.href
      if (url === 'http://localhost:8080/#/Hospital' || 'http://cumulus-dashboard.dev.api.paradymelab.com/#/Hospital'){
        axios.get('http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start='+this.serviceStartDate+'&end='+this.serviceEndDate)
        .then(response => {
          console.log(response)
          let data = []
          var i
          for (i = 0; i < response.length; i++){
            data.push(response.data[i])
          }
          this.table = data
          console.log(this.table)
          // this.hospitalCount = response.data.length
          this.hospitalCount = this.table.length
        // http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start='+this.startDate+'&end='+this.endDate)
        })
      } else {
        axios.get('http://cumulus-error-processor-api-dev-cumulus.labcontainers.census.gov/error/details/query_range?query=firstTriedTS&start='+this.serviceStartDate+'&end='+this.serviceEndDate)
        .then(response => {
          console.log(response)
          this.table = response.data
          console.log(this.table)
          this.hospitalCount = response.data.length
        // http://cumulus-error-processor.dev.api.paradymelab.com/error/details/query_range?query=firstTriedTS&start='+this.startDate+'&end='+this.endDate)
        })
      }
    }
  }
}
</script>

